   <div class="air-info">{{$t('index.本著完全去中心化的原則，符合以下條件的')}}<br>{{$t('index.將得到為期')}}<span><b>{{paradrop_rounds}}</b>{{$t('index.輪')}}</span>{{$t('index.的空投')}} </div>
   <div class="sign-box">
     <div class="title flex-middle">
