       </div>
       <template
        v-if="!info || !info.data.registered"
       >
       <template v-if="isDapp">
         <div class="small-btn" v-if="!signature" @click="signCheck">{{$t('index.授權校驗')}}</div>
       <div class="small-btn" v-else @click="signCheck">{{$t('index.再次校驗')}}</div>
       </template>
      
       </template>
       
     </div>
     <div class="condition-box">
       <div class="desc">{{$t('index.校驗條件')}}</div>
       <div class="condition-item flex-between" :class="checkInfo && checkInfo.data.reg_time || (info && info.data.registered) ? 'success' : ''">
        <span class="flex-item"> {{$t('index.年月日之前使用過的錢包地址', {y: require_register_date[0], m: require_register_date[1], d: require_register_date[2]})}}</span>
        <img v-if="checkInfo && !isLoading && checkInfo.data.reg_time || (info && info.data.registered)" src="@/assets/images/yes.png" alt="">
        <img v-if="checkInfo && !isLoading && !checkInfo.data.reg_time && (info && !info.data.registered)" src="@/assets/images/no.png" alt="">
        <van-loading class="spinner" v-if="isLoading" type="spinner" color="#7898E7" size="20px"/>
       </div>
       <div class="condition-item flex-between" :class="checkInfo && checkInfo.data.balance || (info && info.data.registered) ? 'success' : ''">
         <span class="flex-item">{{$t('index.錢包餘額大於BNB', {num: require_balance})}}</span>
          <img v-if="checkInfo && !isLoading && checkInfo.data.balance || (info && info.data.registered)" src="@/assets/images/yes.png" alt="">
        <img v-if="checkInfo && !isLoading && !checkInfo.data.balance && (info && !info.data.registered)" src="@/assets/images/no.png" alt="">
        <van-loading class="spinner" v-if="isLoading" type="spinner" color="#7898E7" size="20px"/>
       </div>
