         <div class="result-btn" :class="{'success': checkInfo && checkInfo.status == 1}">
           {{$t('index.校驗失敗')}}
         </div>
         <div class="msg-box">
           {{checkInfo.info}}，{{$t('index.请切换符合条件的钱包。')}}<br>{{$t('index.此錢包地址今日還可校驗次', {num: checkInfo.data.available_times})}}
         </div>
       </div>
        <div class="result" v-if="checkInfo && checkInfo.status == 1">
         <div class="result-btn" :class="{'success': checkInfo && checkInfo.status == 1}">
           {{$t('index.校驗成功')}}
         </div>
         <div class="msg-box">
          {{$t('index.請填寫接收空投的主網地址')}}
         </div>
       </div>
     </div>
   </div>
