       <div class="item" @click="toUrl">{{$t('index.亞洲區下載')}}</div>
       <div class="item" @click="toUrl">{{$t('index.美洲區下載')}}</div>
       <div class="item" @click="toUrl">{{$t('index.歐洲區下載')}}</div>
     </div>
   </div>
    <!-- <van-button :loading="loading" size="large" :loading-text="''" type="primary" @click="sign">sign</van-button>
    <van-button :loading="loading" size="large" :loading-text="''" type="primary" @click="signCheck">signCheck</van-button>
    <van-button :loading="subLoading" size="large" :loading-text="''" type="primary" @click="signInfo">signInfo</van-button>
    <van-button :loading="subLoading" size="large" :loading-text="''" type="primary" @click="sign">submit</van-button> -->
  </div>
</template>
<script>
import Header from '@/components/Header'
import { reactive, toRefs, computed, watch, onMounted } from 'vue'
import { useI18n } from "vue-i18n"
import { useStore } from "vuex"
import { signEnroll, signCheck, signInfo, findInfo } from "@/api"
import { Toast } from "vant";
import { CountTo } from 'vue3-count-to';

export default {
  components: {
    Header,
    CountTo
  },
  setup () {
    const store = useStore()
    const { t } = useI18n()
    
    // const route = useRoute
    const data = reactive({
      locale: computed(() => store.state.locale),
      address: computed(() => store.state.account),
      web3: computed(() => store.state.provider),
      isDapp: computed(() => store.state.isDapp),
      exploreClose: false,
      loading: false,
      subLoading: false,
      require_register_date: [0,0,0],
      paradrop_list: [],
      current_rounds: 0,
      fsv_height: 0,
      paradrop_rounds: 0,
      require_balance: 0,
      require_register_max_height: 0,
      has_ended: false,
      isCheck: false,
      checkInfo: null,
      info: null,
      signature: '',
      fsvAddress: '',
      isLoading: false,
      privateKey: '',
      initContract: () => {
        data.initData()
      
      },
       toUrl: () => {
        window.location.href = 'https://dsw3rxqm9wq1x.cloudfront.net/app/bql_release_v22.05.04.apk'
      },
      initData: () => {
        data.findInfo()
      },
      sign: async () => {
        if(!data.fsvAddress) {
          Toast(t('index.请填写正确FSV主网地址'))
          return
        }
        try {
          let result = await signEnroll({sign: data.signature.substring(2, data.signature.length), bsc: data.address, lang: data.locale, fsv: data.fsvAddress})
          if(result.status == 0) {
            Toast(result.info)
          } else {
            Toast(t('index.登記成功'))
            data.checkInfo = null
            data.findInfo()
          }
        } catch (error) {
          console.log(error)
        }
        
      },
      privateKeySign: async () => {
        if(!data.privateKey) {
          Toast(t('index.请填寫符合條件的BSC錢包私鑰,進行授權驗證'))
          return
        }
        let accountInfo;
        try {
         accountInfo = data.web3.eth.accounts.privateKeyToAccount(data.privateKey)
        } catch (error) {
          Toast(t('index.私鑰格式錯誤'))
          return
        }
        console.log(accountInfo)
        data.privateKey = accountInfo.privateKey
        store.commit("updateAccount", accountInfo.address);
        let nonce = data.address.toLowerCase() + '.paradrop.enroll'
        try {
          let signature = await data.web3.eth.accounts.sign(data.web3.utils.toHex(nonce), data.privateKey)
          signature = signature.signature
         data.isLoading = true
          let info = await signCheck({sign: signature.substring(2, signature.length), bsc: data.address, lang: data.locale})
          data.isLoading = false
          data.checkInfo = info
          data.signature = signature
        } catch (error) {
          console.log(error)
          data.isLoading = false
        } 
      },
      findInfo: async () => {
        let info = await findInfo({bsc: data.address, lang: data.locale})
        data.fsvAddress = info.data.fsv_addr
        data.info = info
      },
      signCheck: async () => {

        if(data.checkInfo && data.checkInfo.data.available_times == 0) {
          Toast(t('index.該地址檢查次數已達到今日上限'))
          return
        }
        let nonce = data.address.toLowerCase() + '.paradrop.enroll'
        try {
          let signature = await data.web3.eth.personal.sign(data.web3.utils.toHex(nonce), data.address)
          data.isLoading = true
          let info = await signCheck({sign: signature.substring(2, signature.length), bsc: data.address, lang: data.locale})
          data.isLoading = false
          data.checkInfo = info
          data.signature = signature
        } catch (error) {
          console.log(error)
          data.isLoading = false
        } 
      },
      signInfo: async () => {
        try {
           let info = await signInfo()
           data.current_rounds = info.data.current_rounds
           data.fsv_height = info.data.fsv_height
           data.has_ended = info.data.has_ended
           data.paradrop_list = info.data.paradrop_list
           data.paradrop_rounds = info.data.paradrop_rounds
           data.require_balance = info.data.require_balance
           data.require_register_date = info.data.require_register_date
           data.require_register_max_height = info.data.require_register_max_height
        } catch (error) {
          console.log(error)
        }
        
      },
      toBottom: () => {
        window.scrollTo(0,5000);
      }
    })
    
    if (data.address) {
      data.initContract()
    }
    data.signInfo()
    onMounted(() => {
       setInterval(() => {
          data.fsv_height = data.fsv_height + 2
        }, 12000)
    })
    watch(() => data.address, (val) => {
      if (val) {
        data.initContract()
      }
    })
    const refData = toRefs(data);
    return {
      ...refData
    };
  }
};
</script>

