import http from "../utils/http";
let domain = "http://paradrop.fs.video:11111";
// let domain = 'https://xs2560.goer.cloud'
// let domain = location.origin
export function signEnroll(params) {
  return http(`${domain}/paradrop/enroll`, {params: params});
}
export function signCheck(params) {
  return http(`${domain}/paradrop/check`, {params: params});
}

export function signInfo() {
  return http(`${domain}/paradrop/info`);
}
export function findInfo(params) {
  return http(`${domain}/paradrop/find`, {params: params});
}
