<template>
  <div class="head-box flex-between">
    <div class="back-box">
      <!-- <img src="@/assets/images/back0.png" alt="" class="back-icon icon0">
      <img src="@/assets/images/back1.png" alt="" class="back-icon icon1"> -->
      <!-- <span class="title">{{title}}</span> -->
      <img src="@/assets/images/logo.png" alt="" class="logo">
    </div> 
    <div class="right-link">
      <img style="margin-right: 19px" @click="showShare = true" src="@/assets/images/share.png" alt="" class="lang">
      <img @click="showLangList = true" src="@/assets/images/lang.png" alt="" class="lang">
      <van-action-sheet v-model:show="showLangList" :actions="langList" @select="changeLang" />
    </div>
     <van-dialog v-model:show="showShare" :title="''" :showConfirmButton="false">
       <div class="share-box">
         <div class="flex-between">
           <span></span>
           <img @click="showShare = false" class="close-img" src="@/assets/images/close.png" />
         </div>
         
      <img class="link-img" src="@/assets/images/link.png" />
      <div class="text">
         {{$t('index.FilSystemVideo空投火熱進行中')}}
         <br>
        
         {{$t('index.歡迎來到區塊鏈上首個影音分享平台')}}
      </div>
      <div class="share-btn clip-btn" :data-clipboard-text="shareText">
        
         {{$t('index.複製分享')}}
      </div>
       </div>
    </van-dialog>

  </div>
</template>

<script>
import {reactive, toRefs, computed, onMounted} from "vue"
import {useStore} from 'vuex'
import {setLang} from "@/utils/i18n"
import { useI18n } from "vue-i18n";
import { Toast } from 'vant';
import Clipboard from 'clipboard'

export default {
  props: ['title'],
  setup () {
    const store = useStore()
    const { t } = useI18n()
   
     var clipboard = new Clipboard('.clip-btn')
      clipboard.on('success', function (e) {
        Toast(t('index.複製成功'))
        e.clearSelection()
      })
      clipboard.on('error', function () {
        Toast(t('index.複製失敗，請手動長按複製'))
      })

    const data = reactive({
      showLangList: false,
      locale: computed(() => store.state.locale),
      showShare: false,
      shareText: '',
      langList: [
        {
          name: '繁體中文',
          lang: 'zh-cn'
        },
        {
          name: 'English',
          lang: 'en'
        }
      ],
      changeLang (item) {
        setLang(item.lang)
        data.showLangList = false
        data.initShareText()
      },
      initShareText: () => {
        data.shareText = `
        ${t('index.FilSystemVideo空投火熱進行中')}
        ${t('index.歡迎來到區塊鏈上首個影音分享平台')}
        ${window.location.origin}
      `
      }
    })
    data.initShareText()
    const refData = toRefs(data)
    return {
      ...refData
    }
  }
}
</script>

<style lang="less" scoped>
.share-box{
  padding: 20px;
  .close-img{
    width: 12px;
    height: 12px;
    display: block;
  }
  .link-img{
    width: 46px;
    display: block;
    margin: 10px auto 15px;
  }
  .text{
    text-align: center;
    color: #111;
    font-size: 15px;
    font-weight: bold;
    line-height: 25px;
  }
  .share-btn{
    width: 240px;
    height: 40px;
    line-height: 40px;
    background: #365BD8;
    border-radius: 10px;
    text-align: center;
    margin: 0 auto;
    color: #fff;
    font-size: 16px;
    font-weight: bold;
    margin-top: 25px;
  }
}
.logo{
  width: 127px;
}
.lang{
  width: 20px;
}
.head-box {
  // position: sticky;
  // z-index: 99;
  // top: 0;
  width: 100%;
  display: flex;
  align-items: center;
  // height: 44px;
  padding: 30px 0 20px;
  .back-box {
    display: flex;
    align-items: center;
  }
  .back-icon {
    width: 10px;
    margin-right: 15px;
  }
  .icon0 {
    display: none;
  }
  .title {
    font-size: 20px;
    color: #333;
  }
  .lan-btn {
    color: #fff;
    background: url('~@/assets/images/lan-arrow.png') no-repeat;
    background-size: 10px 10px;
    background-position: right center;
    padding-right: 15px;
  }
  &.theme {
    // background: #572CD8;
    .icon0 {
      display: block;
    }
    .icon1 {
      display: none;
    }
    .title {
      color: #fff;
    }
  }
}
</style>
